.cartao-pokemon {
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  border-radius: 10px;
}
.cartao-pokemon .cartao-topo {
  padding: 30px 40px 0;
}
.cartao-pokemon .nome {
  margin-bottom: 5px;
  
}
.cartao-pokemon .detalhes {
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.cartao-pokemon .numeracao {
  font-size: 12px;
}
.cartao-pokemon .cartao-imagem {
  width: 350px;
  height: 350px;
}
.cartao-pokemon img {
  max-height: 100%;
}
.cartao-pokemon .cartao-informacoes {
  background-color: #ffffff;
  display: flex;
  justify-content: space-around;
  padding: 80px 30px 50px;
  margin-top: -70px;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}
.cartao-pokemon .cartao-informacoes h3 {
  font-size: 20px;
  margin-bottom: 10px;
  border-bottom: 1px solid #6b727a;
}
.cartao-pokemon .cartao-informacoes .status,
.cartao-pokemon .cartao-informacoes .tipo {
  padding: 0 10px;
  width: 180px;
}
.cartao-pokemon .cartao-informacoes .habilidades ul li,
.cartao-pokemon .cartao-informacoes .status ul li {
  border-bottom: 1px solid #c3c4c5;
  padding: 0 0 5px;
  margin-bottom: 5px;
  font-size: 15px;
}

