body {
  font-family: 'Poppins', sans-serif;
  background-image: url(../assets/imagens/pokemon_wallpaper.webp);
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
}
main {
  min-height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
.pokedex {
  display: flex;
  gap: 30px;
  padding: 10px 0;
}
html {
  scroll-behavior: smooth;
}


@media (max-width: 750px) {
  body {
    font-size: 12px;
    background-image: url(../assets/imagens/pokeball-responsive.jpg);
    background-repeat: no-repeat;
    background-size: contain;
    min-height: 50vh;
  }
  main {
    flex-direction: column;
    padding: 20px;
  }
  .cartao-pokemon {
    max-width: 300px;
  }
  .cartao-pokemon .cartao-imagem {
    width: 200px;
    height: 200px;
  }
  .cartao-pokemon img {
    width: 100%;
    height: 100%;
  }
  .cartao-pokemon .cartao-informacoes {
    padding: 60px 30px 30px;
    margin-top: -40px;
    font-size: 13px;
  }
  .cartao-pokemon .cartao-informacoes .status ul li {
    font-size: 10px;
  }

  .cartao-pokemon .cartao-informacoes .informacoes {
    display: flex;
    justify-content: space-between;
    padding: 0;
  }
  .cartao-pokemon .cartao-informacoes h3 {
    font-size: 16px;
  }

  .listagem {
    display: flex;
    gap: 8px;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 300px;
    max-height: 130px;
  }
  .listagem .pokemon {
    width: 140px;
    max-height: 100px;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    margin-bottom: 0;
    border-radius: 8px;
  }
  .listagem .pokemon span {
    padding-right: 5px;
  }
}
