.listagem {
  max-width: 200px;
  max-height: 93vh;
  overflow-y: auto;
  overflow-x: hidden;
  border-radius: 5px;
  background-color: transparent;
  margin-top: 10px;
}
::-webkit-scrollbar {
  width: 0px;
}

.listagem .pokemon {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
  background-color: rgb(173, 168, 168);
  color: #fff;
  padding: 0 5px;
  cursor: pointer;
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
  min-height: 30px;
  box-shadow: rgba(100, 100, 111, 0.1) 0px 7px 29px 0px;
  transition: 0.15s ease-in-out;
}

.listagem .pokemon:hover {
  transform: scale(1.08);
}

.listagem .pokemon span {
  padding: 20px;
}

.listagem .pokemon.ativo {
  /* background-color: #fa6668; */
  background-color: #00E278;
}

.listagem .pokemon img {
  width: 25%;
  border-radius: 50%;
}

.pesquisar {
  position: relative;
  padding: 0;

  margin: 0;
  min-height: 30px;
  width: 200px;
  border: solid 1px;
  border-radius: 2px;
  font-family: 'Poppins', sans-serif;
  background-color: white;
}
